    }

    #main-wrapper.show-sidebar .left-sidebar {
        left: 0;
    }

    #main-wrapper[data-layout='vertical'][data-sidebar-position='fixed'] .topbar .top-navbar .navbar-collapse {
        position: relative;
        top: 70px;
    }

    #main-wrapper[data-layout='vertical'][data-header-position='fixed'][data-sidebar-position='fixed'] .topbar .top-navbar .navbar-collapse {
        position: relative;
        top: 70px;
    }

    .left-part {
        left: -260px;
        background: #eef5f9;
        z-index: 1;
        top: 70px;
        height: calc(100vh - 66px);
        box-shadow: 0 0 50px rgba(0, 0, 0, 0.2);
        position: fixed;
        transition: 0.1s ease-in;
    }

    .left-part.show-panel {
        left: 0px;
    }

    .reverse-mode .left-part {
        right: -260px;
        left: auto;
    }

    .reverse-mode .left-part.show-panel {
        right: 0px;
    }

    .right-part {
        width: 100%;
        margin-left: 0px;
        height: 500px;
    }
}

.topbar {
    position: relative;
    z-index: 50;
    background: #fff;
    height: 70px;
}

.topbar .navbar-collapse {
    padding: 0px 10px 0 0;
}

.topbar .top-navbar {
    min-height: 70px;
    padding: 0px;
}

.topbar .top-navbar .dropdown-toggle::after {
    display: none;
}

.topbar .top-navbar .navbar-header {
    line-height: 70px;
}

.topbar .top-navbar .navbar-header .navbar-brand {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0px;
    padding: 0 10px 0 10px;
}

.topbar .top-navbar .navbar-header .navbar-brand .dark-logo {
    display: none;
}

.topbar .top-navbar .navbar-header .navbar-brand .logo-icon {
    margin-right: 5px;
}

.topbar .top-navbar .navbar-nav>.nav-item>.nav-link {
    padding: 0px 15px;
    font-size: 19px;
    line-height: 70px;
    height: 70px;
}

.topbar .top-navbar .navbar-nav>.nav-item {
    transition: 0.2s ease-in;
}

.topbar .top-navbar .navbar-nav>.nav-item:hover {
    background: rgba(0, 0, 0, 0.03);
}

.topbar .user-dd,
.topbar .mailbox {
    min-width: 280px;
}

.topbar .nav-toggler,
.topbar .topbartoggler {
    color: #fff;
    padding: 0 15px;
    border: 0;
}

.search-box .app-search {
    position: absolute;
    height: 200px;
    background-color: #fff;
    background-color: var(--white);
    margin-right: 0px;
    display: block;
    z-index: 110;
    width: 100%;
    top: -1px;
    box-shadow: 2px 0px 10px rgba(0, 0, 0, 0.2);
    display: none;
    left: 0px;
}

.search-box .app-search input {
    width: 100%;
    min-height: 70px;
    border-radius: 0px;
    font-size: 17px;
    transition: 0.5s ease-in;
}

.search-box .app-search .srh-btn {
    position: absolute;
    top: 23px;
    cursor: pointer;
    background: #fff;
    width: 15px;
    height: 15px;
    right: 20px;
    font-size: 14px;
    border: 0;
}

.search-box .app-search .search-btn {
    position: absolute;
    top: 170px;
    cursor: pointer;
    background: #fff;
    width: 15px;
    height: 15px;
    right: 50%;
    font-size: 14px;
    border: 0;
}

.search-box .app-search.show-search {
    display: block;
}

/*******************
Megamenu
******************/
.topbar .mega-dropdown {
    position: static;
}

.topbar .mega-dropdown .dropdown-menu {
    padding: 30px;
    width: 100%;
    max-height: 480px;
    overflow: auto;
}

/*******************
Designed dropdown
******************/
.topbar .dropdown-menu {
    padding-top: 0px;
    border: 0px;
    font-size: 1rem;
    box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.1);
}

.topbar .dropdown-menu .with-arrow {
    position: absolute;
    height: 10px;
    overflow: hidden;
    width: 40px;
    top: -10px;
}

.topbar .dropdown-menu .with-arrow>span {
    background-color: #fff;
    width: 15px;
    height: 15px;
    top: 3px;
    left: 15px;
    position: absolute;
    content: '';
    border-radius: 6px 0 0 0;
    /*box-shadow:-3px -3px 8px rgba(0, 0, 0, 0.1);    */
}

.topbar .dropdown-menu.dropdown-menu-right .with-arrow {
    right: 0px;
}

.topbar .dropdown-menu.dropdown-menu-right .with-arrow>span {
    right: 20px;
    left: auto;
}

/*******************
Topbar Responsive
******************/
@media (max-width: 767px) {

    .topbar .top-navbar .navbar-collapse.show,
    .topbar .top-navbar .navbar-collapse.collapsing {
        display: block;
        width: 100%;
        border-top: 1px solid #dee2e6;
    }

    .topbar .top-navbar .navbar-nav {
        flex-direction: row;
    }

    .topbar .top-navbar .navbar-nav .dropdown {
        position: static;
    }

    .topbar .top-navbar .navbar-nav>.nav-item>.nav-link {
        padding: 0 10px;
    }

    .topbar .top-navbar .navbar-header {
        display: flex;
        align-items: center;
        width: 100%;
        justify-content: space-between;
    }

    .topbar .top-navbar .dropdown-menu {
        position: absolute;
        width: 100%;
    }

    .topbar .top-navbar .dropdown-menu .with-arrow {
        display: none;
    }
}

